	module.exports = {"data":{"markdownRemark":{"html":"<h2>What is participatory design?</h2>\n<p>Participatory design research involves inviting users, developers, and stakeholders to play an active role during the design process. Participants bring their own views of the problem and may offer ideas or possibilities that were not evident.</p>\n<h2>How to conduct a participatory design session:</h2>\n<p>Give the users a blank canvas to create a site that works for them and provide a list of known elements that will be on the page. ask the user to prioritize where sections would go on a page, down to the detail of how a widget looks and what it contains. after the exercise is complete, ask the participants to explain why they envisioned the site the way they did. A participatory design session is typically done prior to any design work. organize a participatory design session when you want to better understand how people think about a given problem.</p>","frontmatter":{"title":"Participatory Design","overview":[{"heading":"Investment","amount":2,"description":"Participatory design in the simplest form ask users and stakeholders to try their hand at designing an interface, website, etc. This can be facilitated by giving them a sheet of paper and a prompt."},{"heading":"Validity","amount":2,"description":"By asking people who don’t typically design things to design something, you may encounter obvious issues with the concept they create. That said, there can often be valuable insights hidden within."},{"heading":"Expertise","amount":4,"description":"Facilitating an e ective participatory design session can be challenging, as you’ll need to convey effectively what tasks need to be supported and assist participants with visualizing their ideas while avoiding priming them."}],"resources":null,"relatedMethods":[{"text":"Focus Group","url":"/focus-group/"},{"text":"Card Sort","url":"/card-sort/"}]},"fields":{"slug":"/participatory-design/"}},"allMarkdownRemark":{"edges":[{"node":{"frontmatter":{"title":"A/B Test","url":"/ab-test/"}}},{"node":{"frontmatter":{"title":"Affinity Diagram","url":"/affinity-diagram/"}}},{"node":{"frontmatter":{"title":"Card Sort","url":"/card-sort/"}}},{"node":{"frontmatter":{"title":"Empathy Map","url":"/empathy-map/"}}},{"node":{"frontmatter":{"title":"Focus Group","url":"/focus-group/"}}},{"node":{"frontmatter":{"title":"Interview","url":"/interview/"}}},{"node":{"frontmatter":{"title":"Journey Map","url":"/journey-map/"}}},{"node":{"frontmatter":{"title":"Participatory Design","url":"/participatory-design/"}}},{"node":{"frontmatter":{"title":"Persona","url":"/persona/"}}},{"node":{"frontmatter":{"title":"Usability Testing","url":"/usability-testing/"}}},{"node":{"frontmatter":{"title":"User Research Guidelines","url":null}}}]}},"pathContext":{"slug":"/participatory-design/"}}

